body {
  margin: 0;
  padding: 0;
}

.main{
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  background-color: rgb(255, 255, 255);
}

#gridContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}

#grid {
  /* background-color: blue; */
  display: flex;
  flex-direction: column;
  gap: 0;
  border: 2px solid black;
  z-index: 99;
}

.row {
  display: flex;
}

.cellContainer {
  display: flex;
}

.cell {
  width: 50px;
  height: 50px;
  z-index: 1;
  background-color: white;
}

.cell.coloured {
  background-color: rgba(255, 0, 130, 1);
}

.cell.colouredPicked {
  background-color: rgba(0, 255, 162, 1);
}

.cell:hover {
  background-color: rgba(0, 255, 162, 0.5);
}

.rowVSep {
  display: flex;
  gap: 0;
}

.segmentH {
  position: 'absolute';
  width: 1px;
  height: 50px;
  background-color: black;
  margin-left: 1px;
  margin-right: 1px;
}

.segmentH.bold {
  width: 3px;
  margin-left: 0;
  margin-right: 0;
}

.segmentV {
  position: 'absolute';
  background-color: black;
  width: 53px;
  height: 1px;
  margin-top: 1px;
  margin-bottom: 1px;
}

.segmentV.bold {
  height: 3px;
  margin-top: 0;
  margin-bottom: 0;
}

.areaPicker {
  margin-left: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.areaPickerButton {
  width: 70px;
  height: 70px;
  margin: 5px;
}

.topPart {
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.gridPropWrapper {
  display: flex;
  flex-direction: row;
  gap: 20px;
}

.gridProp {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.gridPropTitle {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}



input {
  height: 25px;
}

.bottomButtons {
  display: flex;
  flex-direction: row;
}

.midButtons {
  display: flex;
  gap: 10px;
}

.btn {
  width: 100%;
  margin-left: 20px;
  margin-right: 20px;
  padding: 10px;
  border-radius: 20px;
  justify-content: center;
  align-items: center;


  /* background-color: rgb(255,151,41); */
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
  font-size: 15px;
}

.primary {
  color: white;
  border: none;
  background-color: rgb(255, 151, 41);
}

.primary.solving{
  animation: solving 0.5s linear infinite;
}

.primary:hover {
  -webkit-box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.18);
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.18);
}

.secondary:hover {
  -webkit-box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.18);
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.18);
}

.secondary {
  color: black;
  border: 2px solid rgb(255, 151, 41);
  background-color: white;
}

.delete {
  color: black;
  border: 2px solid red;
  background-color: white;
}

.delete:hover {
  -webkit-box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.18);
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.18);
}

.separator {
  width: 100%;
  height: 1px;
  background-color: lightgray;
  opacity: 0.5;
}

.loading{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px;
}

.loadingText{
  font-size: 50px;
  font-weight: bold;
  text-align: center;
}

.lds-dual-ring {
  display: inline-block;
  width: 50px;
  height: 50px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 34px;
  height: 34px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes solving {
  0% {
    background-color: rgba(255, 151, 41, 1);
  }
  50%{
    background-color: rgba(255, 151, 41, 0.3);
  }
  100% {
    background-color: rgba(255, 151, 41, 1);
  }
}
